

























































.field {
  .field-display {
    > svg.svg-inline--fa, > .btn-edit svg.svg-inline--fa {
      margin-left: 6px;
      font-size: 0.875em;
      vertical-align: 0;
    }
  }
  tr {
    .field-display {
      > svg.svg-inline--fa {
        margin-left: 4px;
        font-size: 10px;
        vertical-align: 0.2em;
      }
    }
  }
}
.uk-fieldset {
  margin: 36px 0 20px;
  h4 {
    margin: 0;
    text-transform: capitalize;
  }
  .titlesep {
    margin: 0 0 12px;
  }
}
