














































































































































.form-page {
  border-radius: 2px;
  height: 100%;
  > .uk-card-header {
    padding: 0;
    border-bottom: none;
    .uk-card-title{
      margin: 6px 14px;
    }
    .form-controls{
      margin-bottom: 0;
    }
    .uk-button {
      font-size: 0.95rem;
      line-height: 34px;
      padding: 0 12px;
    }
    .uk-button-group {
      .uk-button:first-child {
        /* border-top-left-radius: 2px; */
        border-bottom-left-radius: 2px;
      }
      .uk-button:last-child {
        border-top-right-radius: 2px;
        /* border-bottom-right-radius: 2px; */
      }
    }
  }
  .uk-card-body {
    height: calc( 100% - 45px );
    box-sizing: border-box;
    padding: 6px 0px 1px 24px;
    > .scroller {
      height: 100%;
      padding-right: 16px;
    }
  }
  &.scrolled .uk-card-body {
    box-shadow: inset 0 12px 11px -11px rgba(0, 0, 0, 0.1);
  }
  .no-data {
    color: rgba(0,0,0,0.35);
  }
  .btn-confirm {
    color: limegreen;
    &:hover {
      color: forestgreen;
    }
  }
  .btn-cancel {
    color: red;
    &:hover {
      color: darkred;
    }
  }
}
